import { HttpFetchService, IMessageResponse, Usuario } from '../../Shared';
import { ConfirmModalMessage } from '../../Shared/Components/ConfirmModalMessage';
import { ConfigureDataTable } from '../../Shared/Components/ConfigureDataTable';
import { JQValidations } from '../../Shared/Utils/JQValidations';
import { SelectorMenu } from '../../Shared/Utils/SelectorMenu';
import { IPage } from '../../Shared/Components/IPage';

export class LogsPage implements IPage {

    private Table: DataTables.Api;
    
    

    BaseURL: "/Log/Index";

    constructor() {
        this.Init();
    }

    public Destroy() {
        this.Table.destroy(false);
        //this.Table.rows().invalidate().draw();
    }

    public Init() {
        //M.toast({ html: "Inicializa Logs", classes: "error" });
        this.InicializarButtons();
        this.InicializarControls();
    }

    private InicializarControls() {
        SelectorMenu.SeleccionEnElMenu();
        this.Table = new ConfigureDataTable().Configure("#data-table-log", [{ "width": "5%", "targets": 0 }]);     

        var Buscar = document.getElementById("search");
        Buscar.addEventListener('click', event => this.BuscarLogs());

        var Insertar = document.getElementById("insert");
        Insertar.addEventListener('click', event => this.Insertar());
    }

    private async BuscarLogs() {

        //M.toast({ html: "Inicia Busqueda", classes: "succes" });

        try {


            var fechaInicial = (<HTMLInputElement>document.getElementById("fechaInicial")).value;
            var horaInicial = (<HTMLInputElement>document.getElementById("horaInicial")).value;
            var fechaFinal = (<HTMLInputElement>document.getElementById("fechaFinal")).value;
            var horaFinal = (<HTMLInputElement>document.getElementById("horaFinal")).value;


            if (fechaInicial != "" && horaInicial != "" && fechaFinal != "" && horaFinal != "") {

                var payload = { fechaInicial: fechaInicial, horaInicial: horaInicial, fechaFinal: fechaFinal, horaFinal: horaFinal };

                const httpService = new HttpFetchService();
                httpService.Post<string>("/Log/BuscarRegistros", payload, false)
                    .then((data) => {
                        if (data) {
                            //$("#log-list").html(data);
                            document.getElementById("log-list").innerHTML = data;
                            this.Table = new ConfigureDataTable().Configure("#data-table-log");
                            let total = this.Table.rows().count();
                            M.toast({ html: "Busqueda finalizada, encontrados: " + total, classes: (true) ? "succes" : "error" });
                        }
                        else
                            M.toast({ html: data, classes: (false) ? "succes" : "error" });
                    }).catch((error) => {
                        M.toast({ html: "No es posible consultar los datos: " + error, classes: "error" });
                        console.log(error)
                    })




            }
            else {
                M.toast({ html: "Formato de fecha incorrecto", classes: (false) ? "succes" : "error" });
            }


            return false; // prevent reload

        }
        catch (error) {
            M.toast({ html: "Formato de fecha incorrecto", classes: (false) ? "succes" : "error" });
        }

    }

    private async Insertar() {
                
        try {



            var fechaInicial = (<HTMLInputElement>document.getElementById("fechaInicial")).value;
            var horaInicial = (<HTMLInputElement>document.getElementById("horaInicial")).value;
            var fechaFinal = (<HTMLInputElement>document.getElementById("fechaFinal")).value;
            var horaFinal = (<HTMLInputElement>document.getElementById("horaFinal")).value;


            var payload = { fechaInicial: fechaInicial, horaInicial: horaInicial, fechaFinal: fechaFinal, horaFinal: horaFinal };

            const httpService = new HttpFetchService();
            httpService.Post<string>("/Log/InsertarLogEjemplo", payload, false)
                .then((data) => {
                    if (data) {
                        //$("#log-list").html(data);
                        //document.getElementById("log-list").innerHTML = data;
                        //this.Table = new ConfigureDataTable().Configure("#data-table-log");
                        //let total = this.Table.rows().count();
                        M.toast({ html: "Insercion finalizada", classes: (true) ? "succes" : "error" });
                    }
                    else
                        M.toast({ html: data, classes: (false) ? "succes" : "error" });
                }).catch((error) => {
                    M.toast({ html: "No es posible consultar los datos: " + error, classes: "error" });
                    console.log(error)
                })

            return false; // prevent reload

        }
        catch (error) {
            M.toast({ html: "Formato de fecha incorrecto", classes: (false) ? "succes" : "error" });
        }

    }

    private InicializarButtons() {
        var table = document.getElementById("data-table-log");
    }

    

    

   

}

var logsPage = new LogsPage();



// Called after every non-initial page load
document.addEventListener('turbolinks:render', (e: any) => {
    if (document.URL.indexOf('/Log/Index') != -1) {
        if (logsPage) {
            logsPage.Init();
        }
        else {
            logsPage = new LogsPage();
        }
        //console.log(e.data.url);
    }
    else
        logsPage?.Destroy();
});